.main-slider {
    position: relative;

    &__numbers{
        position: absolute;
        top: 30px;
        right: 70px;
        z-index: 100;
        font-size: 40px;
        font-weight: bold;
        color: #fff;
        span{
            font-size: 20px;
        }
    }

    &__slider {}

    &__item {
        height: 600px;
        position: relative;
        border-bottom: 75px solid #eee;
    }

    &__img {
        position: absolute;
        right: 0;
        bottom: -60px;
        z-index: 10;
    }

    &__canvas {
        background: rgba(255, 255, 255, 0.8);
        backdrop-filter: blur(2px);
        width: 50%;
        height: 100%;
        z-index: 0;

        position: absolute;
        top: 0;
        left: 0;
    }

    .container {
        height: 100%;
    }

    &__container-wrap {
        position: relative;
        z-index: 2;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    &__title {
        font-family: 'PT Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 44px;
        line-height: 141.69%;
        color: #2D2D2D;
        margin-bottom: 30px;
        width: 555px;
        position: relative;
        &:before{
            content: '';
            display: block;
            width: 264px;
            height: 5px;
            background: #FCB427;
            position: absolute;
            top: -20px;
            left: 0;
            
        }
    }

    &__subtitle {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 156.69%;
        color: #2D2D2D;
        margin-bottom: 30px;
        width: 435px;
    }

    &__btn-wrap {
        display: flex;
    }

    &__btn {
        margin-right: 28px;
    }
}

@media screen and (max-width: 1100px) {
    .main-slider {
        &__title {
            width: 444px;
        }
    }
}
@media screen and (max-width: 1045px) {
    .main-slider {
        &__img {
            display: none;
        }
    }
}

@media screen and (max-width: 1024px) {
    .main-slider {
        &__canvas {
            background: rgba(255, 255, 255, 0.8);
            backdrop-filter: blur(2px);
            width: 70%;
            height: 100%;
            z-index: 0;

            position: absolute;
            top: 0;
            left: 0;
        }

        &__title {
            width: 444px;

        }
    }
}
@media screen and (max-width: 730px) {
    .main-slider {
        &__btn-wrap {
            flex-direction: column;
            align-items: baseline

        }
    
        &__btn {
            margin-right: 0px;
            margin-bottom: 30px;
        }
    }
}
@media screen and (max-width: 635px) {
    .main-slider {
        &__canvas {
            width: 100%;
        }
        &__btn-wrap {
            flex-direction: column;
            align-items: center

        }
    
        &__btn {
            margin-right: 0px;
            margin-bottom: 30px;
        }
        &__title {
            width: 100%;
    
        }
    
        &__subtitle {
            width: 100%;
        }
    }
}

@media screen and (max-width: 450px) {
    .main-slider {
        &__item {
            height: 700px;
        }
    }
}

@media screen and (max-width: 450px) {
    .main-slider {
        &__title {
            &:before{
                width: 160px;
            }
        }
    }
}

.slick-arrow{
    position: absolute;
    z-index: 100;
    transform: translate(0, 0);
    top: calc(100% - 141px);
    
    background-color: #FCB427;
    width: 66px;
    height: 66px;
}

.slick-prev, .slick-next{}

.slick-prev:before, .slick-next:before{
    content: '';
}

.slick-prev{
    left: calc(50% - 66px);
    background: url("../img/arrow-prev.png") no-repeat 50% !important;
    border: 1px solid #fff !important;
    &:hover{
        background: url("../img/arrow-prev.png") no-repeat 50%;        
    }
}
.slick-next{
    left: 50%;
    background: url("../img/arrow-next.png") #FCB427 no-repeat 50% !important;
    border: 1px solid transparent;
    &:hover{
        background: url("../img/arrow-next.png") #FCB427 no-repeat 50%;
    }
}

@media screen and (max-width: 635px) {
    .main-slider__numbers{
        display: none;
    }
    .slick-arrow{
        display: none;
    }
}