.button-color {
  display: inline-block;
  border: 2px solid transparent;
  background-color: #FCB427;
  padding: 15px 45px;
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 141.69%;
  color: #FFFFFF;
  transition: 0.5s;
}
.button-color:hover {
  border: 2px solid #FCB427;
  background-color: transparent;
  color: #000;
}

.button-transparent {
  display: inline-block;
  background-color: transparent;
  border: 1px solid #2D2D2D;
  padding: 15px 45px;
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 141.69%;
  color: #2D2D2D;
  transition: 0.5s;
}
.button-transparent:hover {
  background-color: #2D2D2D;
  color: #fff;
}

.my-btn {
  font-size: 22px;
  line-height: 28px;
  text-transform: uppercase;
}

@media screen and (max-width: 768px) {
  .my-btn {
    font-size: 18px;
    line-height: 26px;
  }
}
@media screen and (max-width: 320px) {
  .my-btn {
    font-size: 12px;
    line-height: 16px;
  }
}
.list-games {
  padding: 20px 0 50px 0;
  background-color: #fff;
}
.list-games__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 50px;
  padding: 30px 0;
}
.list-games__item {
  text-align: center;
}
.list-games__item-img {
  width: 250px;
  height: 250px;
  transition: 0.5s;
}
.list-games__item-img:hover {
  transform: scale(1.1);
  transition: 0.7s;
}
.list-games__item-name {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  color: #000;
  padding-top: 25px;
}

.fancybox__content {
  background-color: #48388b;
  padding: 0;
}

.form__body {
  padding: 50px;
}

.form__logo img {
  display: block;
  margin: 0 auto;
  margin-bottom: 40px;
}

.form__item {
  color: #fff;
  margin-bottom: 40px;
}

.form__item input {
  display: block;
  margin: 0 auto;
}

.form__input {
  font-size: 18px;
  height: 50px;
  padding: 0px 10px;
  margin-left: 5px;
  border: 2px solid #333;
  border-radius: 7px;
}

.form__btn {
  background-color: #202020;
  color: #fff;
  font-size: 18px;
  padding: 10px 30px;
  border: none;
  border-radius: 7px;
  display: block;
  margin: 0 auto;
  cursor: pointer;
  text-transform: uppercase;
}

.form__btn:hover {
  background-color: #f38824;
}

@media screen and (max-width: 480px) {
  .form__body {
    padding: 30px;
  }
}
.header {
  padding: 20px 0;
}
.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__find {
  position: relative;
}
.header__find input {
  width: 270px;
  font-size: 16px;
  padding: 5px 15px;
  border: 1px solid rgba(45, 45, 45, 0.6);
}
.header__find img {
  position: absolute;
  top: 8px;
  right: 10px;
}

.topnav-mobile {
  display: none;
  width: 100vw;
  height: calc(100vh - 95px);
  background-color: #888;
  position: fixed;
  top: 95px;
  left: 0;
  z-index: 99;
}
.topnav-mobile .menu-header-mobile {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 80px 0;
  background-color: rgba(0, 0, 0, 0.8);
}
.topnav-mobile .menu-item-mobile a {
  font-size: 20px;
  color: #d2d2d2;
}
.topnav-mobile .menu-item-mobile a:hover {
  color: #888;
}

.topnav-mobile.responsive {
  display: block;
}

@media screen and (max-width: 580px) {
  .topnav-mobile {
    height: calc(100vh - 175px);
    top: 175px;
  }
}
.menu-burger {
  display: none;
  width: 40px;
  height: 28px;
  cursor: pointer;
  z-index: 100;
  position: relative;
  transition: 0.5s;
}
.menu-burger span {
  display: block;
  width: 40px;
  height: 4px;
  background-color: #2d2d2d;
}
.menu-burger:before {
  content: "";
  display: block;
  width: 40px;
  height: 4px;
  background-color: #2d2d2d;
  position: absolute;
  top: 12px;
  left: 0;
  transform: rotate(0deg);
  transition: 0.3s;
}
.menu-burger:after {
  content: "";
  display: block;
  width: 40px;
  height: 4px;
  background-color: #2d2d2d;
  position: absolute;
  top: 24px;
  left: 0;
  transform: rotate(0deg);
  transition: 0.3s;
}

.menu-burger.open-menu span {
  opacity: 0;
}

.menu-burger.open-menu:before {
  transform: rotate(45deg);
  background-color: #fff;
}

.menu-burger.open-menu:after {
  transform: rotate(-45deg);
  top: 12px;
  background-color: #fff;
}

.menu-header {
  display: flex;
  gap: 30px;
}

.menu-item a {
  font-weight: 500;
  font-size: 16px;
  line-height: 156.69%;
  color: #000000;
}

@media screen and (max-width: 840px) {
  .header__find input {
    width: 200px;
  }

  .menu-header {
    display: flex;
    gap: 25px;
  }

  .menu-item a {
    font-weight: 500;
    font-size: 16px;
    line-height: 156.69%;
    color: #000000;
  }
}
@media screen and (max-width: 768px) {
  .menu-burger {
    display: block;
  }

  .menu-header {
    display: none;
  }
}
.main-slider {
  position: relative;
}
.main-slider__numbers {
  position: absolute;
  top: 30px;
  right: 70px;
  z-index: 100;
  font-size: 40px;
  font-weight: bold;
  color: #fff;
}
.main-slider__numbers span {
  font-size: 20px;
}
.main-slider__item {
  height: 600px;
  position: relative;
  border-bottom: 75px solid #eee;
}
.main-slider__img {
  position: absolute;
  right: 0;
  bottom: -60px;
  z-index: 10;
}
.main-slider__canvas {
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(2px);
  width: 50%;
  height: 100%;
  z-index: 0;
  position: absolute;
  top: 0;
  left: 0;
}
.main-slider .container {
  height: 100%;
}
.main-slider__container-wrap {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.main-slider__title {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 44px;
  line-height: 141.69%;
  color: #2D2D2D;
  margin-bottom: 30px;
  width: 555px;
  position: relative;
}
.main-slider__title:before {
  content: "";
  display: block;
  width: 264px;
  height: 5px;
  background: #FCB427;
