.slick-arrow {
  position: absolute;
  z-index: 100;
  transform: translate(0, 0);
  top: calc(100% - 141px);
  background-color: #FCB427;
  width: 66px;
  height: 66px;
}

.slick-prev:before, .slick-next:before {
  content: "";
}

.slick-prev {
  left: calc(50% - 66px);
  background: url("../img/arrow-prev.png") no-repeat 50% !important;
  border: 1px solid #fff !important;
}
.slick-prev:hover {
  background: url("../img/arrow-prev.png") no-repeat 50%;
}

.slick-next {
  left: 50%;
  background: url("../img/arrow-next.png") #FCB427 no-repeat 50% !important;
  border: 1px solid transparent;
}
.slick-next:hover {
  background: url("../img/arrow-next.png") #FCB427 no-repeat 50%;
}

